* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	outline: none;
}

body {
	font-family: $ptmono;
	font-style: normal;
	font-weight: normal;
	line-height: normal;
	letter-spacing: normal;
}

.wrapper {
	overflow: hidden;
	width: 100%;
	height: 100%;
}

.container {
	width: 100%;
	margin: 0 auto;

	@include min-media(1920px) {
		max-width: 1920px;
	}
}

// Header start
#header {
	width: 100%;
	position: absolute;
	z-index: 2;
	top: 0;
	left: 0;
	background: local;
	padding: 20px 0;

	.header-wrap {
		display: flex;
		align-items: start;
		justify-content: space-between;

		a.logo {
			color: #FFF;
			font-size: 50px;
			font-weight: 700;
			text-decoration: none;

			span {
				font-size: 50px;
				font-weight: 400;
			}
		}

		.header-wrap__right {
			display: flex;

			.header-about {
				width: 100%;
				max-width: 381px;
				display: flex;
				flex-direction: column;
				row-gap: 20px;
			}

			.header-link {
				display: flex;
				align-items: start;
				column-gap: 20px;
			}
		}
	}
}

.header-link {
	a {
		text-decoration: none;
		color: #FFF;
		font-size: 20px;
		font-weight: 400;

		span {
			display: block;
			font-size: 8px;
		}
	}
}

.header-about {
	h4 {
		color: #FFF;
		font-size: 20px;
		font-weight: 400;

		&:not(:first-child) {
			color: #D8D8D8;
		}
	}
}

// Header end

// Home start
#home {
	width: 100%;
	height: 100%;
}

.home-splide {
	.home-slide_img {
		width: 100%;
		height: 100%;

		img {
			width: 100%;
			height: 100vh;
			object-fit: cover;
		}
	}
}

.splide__arrow svg {
	display: none;
}

.splide__arrow {
	width: 120px;
	height: 120px;

	img {
		width: 100%;
		height: 100%;
	}
}

.splide__arrow--prev {
	background: url(../img/arrow-left.svg)no-repeat;
}

.splide__arrow--next {
	background: url(../img/arrow-right.svg)no-repeat;
}

// Home end

// Home bottom start
#home-bottom {
	margin: 30px 0;

	.block-items {
		display: flex;
		column-gap: 20px;

		.item {
			width: 100%;
			height: 100%;
			max-height: 100%;
			padding: 22px 100px 50px 100px;
			display: flex;
			align-items: flex-end;

			&:first-child {
				background-color: #7520B5;
			}

			&:nth-child(2) {
				background-color: #1B49B3;
			}

			&:last-child {
				background-color: #686B6F;
			}
		}
	}
}

.block-items {
	.item {
		.item-about {
			display: flex;
			flex-direction: column;
			row-gap: 30px;

			p {
				color: #FFF;
				font-size: 16px;
				font-weight: 400;
			}
		}
	}
}

.item-about__links {
	li {
		list-style: none;
		display: flex;
		align-items: center;
		color: #fff;

		a {
			color: #FFF;
			text-decoration: none;
			font-size: 14px;
		}
	}
}

// Home bottom end